import React from 'react';
import style from './Users.module.css';
import Pagination from '../common/Pagination/Pagination';
import User from './User';


const Users = (props) => {
    // debugger;
    return (
        <div className={style.users}>
            <div className={style.pagination}>
                <Pagination totalItemsCount={props.totalCount} pageSize={props.pageSize} currentPage={props.currentPage} onChangePage={props.onChangePage} />
            </div>
            <div className={style.title}>Users</div>
            <div className={style.users__container}>
                {props.users.map(elem => (
                    <User id={elem.id} photos={elem.photos} followingInProgress={props.followingInProgress} unFollow={props.unFollow}
                        follow={props.follow} followed={elem.followed} name={elem.name} status={elem.status} />
                ))}
            </div>
        </div>
    )
}

export default Users;
