import React, { useState } from 'react';
import style from './Pagination.module.css';


const Pagination = ({ totalItemsCount, pageSize, currentPage, onChangePage, portionSize = 10 }) => {

    let pageCouter = Math.ceil(totalItemsCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pageCouter; i++) {
        pages.push(i);
    }


    const portionCounter = Math.ceil(pageCouter / portionSize);
    const [portionNumber, setPortionNumber] = useState(1);
    const leftBoundaryPortion = (portionNumber - 1) * portionSize + 1;
    const rightBoundaryPortion = portionNumber * portionSize;

    return (
        <div className={style.pagination}>
            {portionNumber > 1 && <button onClick={() => { setPortionNumber(portionNumber - 1) }}>PREV</button>}
            {pages.filter(page => page >= leftBoundaryPortion && page <= rightBoundaryPortion)
                .map(page => {
                    return <span onClick={() => { onChangePage(page) }} className={`${currentPage === page && style.current_page} ${style.other_page}`}>{page}</span>
                })}
            {portionCounter > portionNumber && <button onClick={() => { setPortionNumber(portionNumber + 1) }}>NEXT</button>}
        </div>
    )
}


export default Pagination;
