import React from 'react';
import style from './Users.module.css';
import userPhotos from './../../assets/images/user.jpg'
import { NavLink } from 'react-router-dom';


const User = ({ id, photos, followed, name, status, followingInProgress, unFollow, follow }) => {
    // debugger;
    return (
        <div className={style.user}>
            <div className={style.user__user}>
                <div><NavLink to={'/profile/' + id} ><img src={photos.small != null ? photos.small : userPhotos} className={style.avatar} /></NavLink></div>
                <div>
                    {followed
                        ? <button disabled={followingInProgress.some(elemId => elemId === id)} onClick={() => {
                            unFollow(id)
                        }}>Unfollow</button>
                        : <button disabled={followingInProgress.some(elemId => elemId === id)} onClick={() => {
                            follow(id)
                        }}>Follow</button>
                    }
                </div>
            </div>
            <div className={style.user__description}>
                <div className={style.user__left}>
                    <div className={style.user__name}>{name}</div>
                    <div className={style.user__status}>{status}</div>
                </div>
                <div className={style.user__right}>
                </div>
            </div>
        </div>
    )
}

export default User;
