import { connect } from "react-redux";
import React from 'react';
import { follow, setCurrentPage, unFollow, followSuccess, unFollowSuccess, toggleFollowingInProgress, requestUsers } from "../../redux/usersReducer";
import Users from "./Users";
import Preloader from "../common/Preloader/Preloader";
import { compose } from "redux";
import { getTotalCount, getCurrentPage, getFollowingProgress, getIsFetching, getPageSize, getUsersSuperSelector } from "../../redux/users-selectors";



class UsersAPIComponent extends React.Component {

    componentDidMount = () => {

        this.props.requestUsers(this.props.currentPage, this.props.pageSize)

    }

    onChangePage = (page) => {
        this.props.requestUsers(page, this.props.pageSize)
    }
    render() {
        return <>
            <Preloader isFetching={this.props.isFetching} />
            <Users
                users={this.props.users}
                totalCount={this.props.totalCount}
                pageSize={this.props.pageSize}
                currentPage={this.props.currentPage}
                follow={this.props.follow}
                unFollow={this.props.unFollow}
                onChangePage={this.onChangePage}
                followingInProgress={this.props.followingInProgress}
                toggleFollowingInProgress={this.props.toggleFollowingInProgress}

            />
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        users: getUsersSuperSelector(state),
        totalCount: getTotalCount(state),
        pageSize: getPageSize(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingProgress(state)
    }
}
export default compose(connect(mapStateToProps, {
    follow,
    unFollow,
    followSuccess,
    unFollowSuccess,
    getTotalCount,
    setCurrentPage,
    toggleFollowingInProgress,
    requestUsers
}))(UsersAPIComponent)
